.baskets {
  margin-bottom: 36px;
}

.baskets__inner {
  @include mobile-center-inner;
}

.baskets__title {
  padding: 0 0 8px 0;
  margin: 0;
}

.baskets__week {
  font-size: 17px;
  font-weight: bold;
  line-height: 1.18;
  letter-spacing: normal;
  color: $color-brand;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 30px;
}

.baskets__description {
  font-size: 15px;
  line-height: 1.6;
  color: $color-danger;
  margin: 0 0 34px 0;
}

.baskets__desc-table {
  border-bottom: 1px solid #cecece;
  color: $color-danger;
}

.baskets__table {
  width: 100%;
  margin: 0 0 28px 0;
  border-collapse: collapse;
}

.baskets__text {
  @include reset-list;
}

.baskets__prise {
  text-align: center;
  font-size: 17px;
  font-weight: bold;
  line-height: 1.41;
  color: $color-danger;
  margin-bottom: 28px;
}

.baskets__to-order-btn {
  @include button-style;
}
